<template>
  <div>
    <div class="m-container">
      <view-header title="会员列表"></view-header>
      <div style="width: 100%" class="flex flex-row flex-start">
        <p style="margin-right: 20px">会员查询:</p>
        <el-input v-model="condition" style="width: 300px" placeholder="用户名/手机号"></el-input>
        <button class="btn" @click="_search">搜索</button>
      </div>
      <div class="m-table-div">
        <table>
          <thead>
            <th>用户ID</th>
            <th>姓名</th>
            <th>性别</th>
            <th>联系方式</th>
            <th>出生年月</th>
            <th>最近成功就诊时间</th>
            <th>操作</th>
          </thead>
          <tbody>
            <tr v-for="item in list" :key="item" @click="dialogVisible2 = true">
              <td>{{item.userId}}</td>
              <td>{{item.realName}}</td>
              <td>{{item.sexStr}}</td>
              <td>{{item.telphone}}</td>
              <td>{{item.brithday | formatDate}}({{item.age}})</td>
              <td>{{item.lastSuccessDate | formatDate2}}</td>
              <td>
                <div class="flex flex-row flex-between" style="width: 160px">
                  <p class="pointer tablebtn" @click.stop="_querySubscription(item.userId)">预约记录</p>
                </div>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <div style="width: 100%; margin: 20px 0" class="flex flex-row flex-end">
        <el-pagination
		  @size-change="_search"
          @current-change="_search"
          :page-sizes="[100, 200, 300, 400]"
          :page-size="100"
		  :current-page.sync="pageNo"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
        </el-pagination>
      </div>
    </div>
    <el-dialog
      title="编辑类别"
      :visible.sync="dialogVisible2"
      width="30%">
      <div class="item flex flex-column" style="width: 100%;">
        <p style="margin-bottom: 20px"><span class="dot">*&nbsp;</span>类别名称</p>
        <el-input v-model="input" style="width: 100%" placeholder=""></el-input>
      </div>
      <div style="width: 100%;margin-top: 20px" class="flex flex-row flex-end">
        <button class="btn" style="width: 100px" @click="dialogVisible2 = false">保存</button>
      </div>
    </el-dialog>
    <el-dialog
      title="预约记录"
      :visible.sync="dialogVisible3"
      width="80%">
      <div class="m-table-div">
        <table>
          <thead>
            <th>订单号</th>
            <th>提交时间</th>
            <th>金额</th>
            <th>预约来源</th>
            <th>状态</th>
          </thead>
          <tbody>
            <tr v-for="item in subList" :key="item" @click="dialogVisible2 = true">
              <td>{{item.subscriptionId}}</td>
              <td>{{item.createTime | formatDate2}}</td>
              <td>¥{{item.subscriptionFee}}</td>
              <td>{{item.subscriptionSource}}</td>
              <td>{{item.statusStr}}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div style="width: 100%; margin: 20px 0" class="flex flex-row flex-end">
        <el-pagination
		  @size-change="_querySubscription(userId)"
          @current-change="_querySubscription(userId)"
          :page-sizes="[100, 200, 300, 400]"
          :page-size="100"
		  :current-page.sync="subPageNo"
          layout="total, sizes, prev, pager, next, jumper"
          :total="subTotal">
        </el-pagination>
      </div>
    </el-dialog>
  </div>
</template>
<script type="text/ecmascript-6">
import ViewHeader from 'base/view-header/view-header'
import {queryUsersInfo,formatDate,querySubscription} from '@/api'
export default {
  components: {
    ViewHeader
  },
  data () {
    return {
      condition: '',
      dialogVisible1: false,
      dialogVisible2: false,
      dialogVisible3: false,
	  pageSizes: [100, 200, 300, 400],
      pageSize: 100,
      total: 0,
      pageNo: 1,
      list: null,
	  userId : 0,
	  subList:null,
	  subTotal:0,
	  subPageNo:1,
	  subPageSize:100
    }
  },
  filters: {
    formatDate (time) {
      var date = new Date(time)
      return formatDate(date, 'yyyy-MM')
    },
    formatDate2 (time) {
      var date = new Date(time)
      return formatDate(date, 'yyyy-MM-dd hh:mm:ss')
    }
  },
  methods: {
	_search () {
      var _this = this
      var params = new URLSearchParams()
      params.append('condition', this.condition)
	  params.append('currentPage', this.pageNo)
	  params.append('pageSize', this.pageSize)
      queryUsersInfo(params).then(function (data) {
        if (!data.success) {
          alert(data.error)
        } else {
          _this.list = data.data
          _this.total = data.total
        }
      }).catch(function (data) {
        alert('系统异常，请稍后再试')
      })
    },
	_querySubscription(val) {
	  var _this4 = this
	  var params = new URLSearchParams()
	  params.append('userId', val)
	  params.append('currentPage', this.subPageNo)
	  params.append('pageSize', this.subPageSize)
	  querySubscription(params).then(function (data) {
        if (!data.success) {
          alert(data.error)
        } else {
		  _this4.userId = val
          _this4.dialogVisible3 = true
          _this4.subList = data.data
          _this4.subTotal = data.total
        }
      }).catch(function (data) {
        alert('系统异常，请稍后再试')
      })
	}
  }
}
</script>
<style lang="stylus" scoped rel="stylesheet/stylus">
@import "~common/stylus/base"
@import "~common/stylus/variable"
.m-container
  @extend .flex
  @extend .flex-column
  width: 100%
  height: 100%
  padding: 0 10px
.green
  color: $color-back-green_0
.tablebtn
  font-size: $size-14
  color: $color-back-green_0
  font-weight: bold
.btn
  border: 0px
  background-color: $color-back-blue_0
  color: $color-back-white_0
  font-weight: bolder
  padding: 0 15px
  margin-left: 10px
  height: 40px
  font-size: $size-16
.item
  width: 48%
  margin-top: 10px
  p
    color: $color-back-black_1
    font-size: $size-14
    .dot
      color: $color-back-blue_2
</style>


